<template>
  <div>
    <vx-header :showback="1" :title="$t('settings.aboutUpdata')"></vx-header>
    <div class="vx-container">
      <div class="content">
        <!-- 概述 -->
        <p class="Overview">
          {{overview}}
        </p>
        <div class="content-message" v-for="item in content">
          <div class="content-header">
            <div class="dian"></div>
            <div>{{item.title}}</div>
          </div>
          <div class="messages">
            {{item.message}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        overview: this.$t('settings.upDataMessage.overView'),
        content: this.$t('settings.upDataMessage.content')
      };
    }
  };
</script>
<style scoped lang="less">
  .content {
    padding: 30px;
    .Overview {
      color: #dddddd;
      margin: 0;
      font-size: 30px;
      text-align: left;
    }
    .content-message {
      margin: 20px 0;
      .content-header {
        display: flex;
        align-items: center;
        font-size: 34px;
        color: #ddd;
        .dian {
          width: 10px;
          height: 10px;
          background: #fff;
          border-radius: 50%;
          margin-right: 8px;
        }
      }
      .messages {
        padding-top: 20px;
        text-align: left;
        color: #dddddd;
        font-size: 28px;
      }
    }
  }
</style>




